  #Register .md-stepper-horizontal .md-step {
    display: table-cell;
    position: relative;
    padding: 6px 0; }
    #Register .md-stepper-horizontal .md-step button {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      padding: 0; }
    #Register .md-stepper-horizontal .md-step:active {
      background-color: rgba(0, 0, 0, 0.04);
      border-radius: 15% / 75%; }
    #Register .md-stepper-horizontal .md-step:first-child:active {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0; }
    #Register .md-stepper-horizontal .md-step:first-child .md-step-bar-left {
      display: none; }
    #Register .md-stepper-horizontal .md-step:last-child:active {
